        padding: 0 30px;
    }
    .call_block{
        height: 100vh;
    }
    section{
        overflow: hidden;
    }
    section .container > span {
        top: 303px;
        left: calc(30px - 100%);
        margin: 0 auto;
        width: 541px;
        height: 30px;
        font-size: 12px;
        line-height: 30px;

    }
}
@media (min-width: 1920px) {
    .container{
        width: 1890px;
    }
    section .container > span{
        font-size: 16px;
    }
    .feedback_button{
        font-size: 16px;
        margin-right: 45px;
    }
}
